import React, {useState} from 'react'
import {
    ServicesContainer,
    ServicesCard,
    ServicesH1,
    ServicesH2,
    ServicesP,
    ServicesWrapper,
    ServicesIcon
} from './RoadMapElements'
import Icon1 from '../../Images/1.svg'
import Icon2 from '../../Images/Vf.svg'
import Icon3 from '../../Images/Account.svg'
import {Button, RaisedButton} from "../../Utilities/ButtonElements";
import {ArrowForward, ArrowRight, HeroBtnWrapper} from "../RoadMap/RoadMapElements";

const RoadMap = () => {


    const [hover, setHover] = useState(false);

    const onHover = () => {
        setHover(!hover);
    }


    return (
        <>
            <ServicesContainer id="road-map">
                <ServicesH1>ROAD MAP</ServicesH1>
                <ServicesWrapper>
                    <ServicesCard>
                        {/*<ServicesIcon src={Icon1} />*/}
                        <ServicesH2>NOVEMBER 2022</ServicesH2>
                        <ServicesP>We help yoy reduce fees and increase your overall revenue.</ServicesP>
                    </ServicesCard>
                    <ServicesCard>
                        {/*<ServicesIcon src={Icon2} />*/}
                        <ServicesH2>DECEMBER 2022</ServicesH2>
                        <ServicesP>You can access our platform anywhere from the world.</ServicesP>
                    </ServicesCard>
                    <ServicesCard>
                        {/*<ServicesIcon src={Icon3} />*/}
                        <ServicesH2>JANUARY 2023
                        </ServicesH2>
                        <ServicesP>Unlock our special membership offers and sove more.</ServicesP>
                    </ServicesCard> <ServicesCard>
                        {/*<ServicesIcon src={Icon3} />*/}
                        <ServicesH2>FEBRUARY 2023</ServicesH2>
                        <ServicesP>Unlock our special membership offers and sove more.</ServicesP>
                    </ServicesCard>
                </ServicesWrapper>
                <HeroBtnWrapper>
                    <RaisedButton

                        as="a" href="https://yeti-ecosystem.gitbook.io/wheely.whitepaper/roadmap-for-wheely" target="_blank"

                        onMouseEnter={onHover}
                        onMouseLeave={onHover}
                        primary="true"
                        dark="true"
                    >
                        OPEN DETAILED ROAD MAP {hover}
                    </RaisedButton>

                </HeroBtnWrapper>
            </ServicesContainer>
        </>
    )
}

export default RoadMap
